import React, { useState } from 'react';
// import './Chatbot.css';

const Chatbot = () => {
  const [userInput, setUserInput] = useState('');
  const [chatHistory, setChatHistory] = useState([]);

  const handleSubmit = () => {
    if (userInput === '') return;

    const newMessage = {
      user: true,
      message: userInput,
    };

    setChatHistory([...chatHistory, newMessage]);
    setUserInput('');

    // Simulate a delayed response from the chatbot
    setTimeout(() => {
      const chatbotResponse = {
        user: false,
        message: 'Chatbot response: ' + userInput,
      };

      setChatHistory([...chatHistory, chatbotResponse]);
    }, 1000);
  };

  const handleChange = (event) => {
    setUserInput(event.target.value);
  };

  return (
    <div className="chatbot-container">
      <div className="chat-history">
        {chatHistory.map((message) => (
          <div key={message.message} className={`chat-message ${message.user ? 'user-message' : 'chatbot-message'}`}>
            {message.message}
          </div>
        ))}
      </div>

      <div className="chatbot-input">
        <input type="text" value={userInput} onChange={handleChange} placeholder="Enter your message" />
        <button onClick={handleSubmit}>Send</button>
      </div>
    </div>
  );
};

export default Chatbot;
